/*============    	АДАПТИВ    ===========*/
.dropdown-toggle{
	border: 1px solid #000;
	padding: 10px 10px;
	border-radius:5px;
	color:#1AB798;
	margin-left: -10px;
	&:hover{
		background-color:#1AB798;
		color:#fff;
	}
	i{
		margin-top: 15px;
		font-size:16px;
	}
}
@media(min-width:0px) and (max-width:320px) {
	nav.navbar-fixed-top {
    	margin-top:10px;
    	.container{
	        h1{
		        font-size:20px;
		        margin:12px;
		    }
		    ul.dropdown-menu{
		    	margin-top:25px;
		        li{
		            padding:10px;
		        }
		    }
		}
	    &.sticky{
	        margin-top:0px;
	        position:fixed;
	        height: 0px;
	        .container{
	            -webkit-box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	            -moz-box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	            box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	        }
	    }
    }
	section#Header{
		padding-top:130px;
		h1{
			font-size: 22px;
			margin-bottom: 10px;
			text-align: center;
		}
		h2{
			font-size: 13px;
			text-align: center;
		}
		h4{
			font-size: 12px;
		}
		button{
	        margin:80px auto 0px auto;    
	        h4{
	            font-size: 16px;
	        }
	    }
	}
	section#Key_Features{
	    margin-bottom:20px;
		h1{
		    font-size:16px;
		}
		.title{
        	margin-top:40px;
        	margin-bottom:40px;
        	img{
            	margin-top: -5px;
       		}
        }
        .paragraph{
        	p{
        		font-size: 12px;
        	}
        }
        .Amet{
		    h4{
		        font-size:14px;
		    }
		    p{
		        font-size:12px;
		    }
		}
		 .Assorted{
		    margin-bottom:0px;
		}
	}
	section#Video{
		text-align:center;
		h3{
			font-size:18px;
		}
		h4{
			font-size: 12px;
		}
		li{
            height: 50px;
            width: 50px;
         }
         ul{
         	align:center;
         	text-align:center;
         }
         ul.list-inline a{
           margin:auto 8px ;
       }
        .video{
        margin-top:30px;
        margin-bottom:40px;
       }
	}
	section#Team{
		text-align:center;
		margin:0 auto;
	     .title{
	         padding-top:40px;
	         padding-bottom:40px;
		     h1{ 
		        font-size:16px;
		        img {
		            margin-top: -5px;
		        }
		    }
		}
		h4{
		    font-size:14px;
		}
		.layer{
        	margin-top:50px;
        	width:99%;
        }
	}
	section#Customers{
		padding-top:30px;
    	padding-bottom:120px;
	    h1{
		    font-size:16px;
		}
		.title{
        	margin-top:20px;
        	margin-bottom:30px;
        	img{
            	margin-top: -5px;
       		}
        }
        .item{
	        h3{
	           font-size:16px;
	        }
	        h5{
	        	margin:5px auto 20px auto;
	        }
	        p{
	        	font-size:10px;
	        }
	    }
	}
	section#Contacts{
		text-align:center;
		.title{
        margin-top:20x;
        margin-bottom:50px;
        img {
            margin-top: -5px;
            }
        h1{
            font-size:16px;
            }
        }
        h2{
        	font-size: 18px;
        	padding-bottom:10px;
        }
        p{
        	font-size: 12px;
        }
         ul{
         	text-align:center;
		}
		.form-group {
            input{
                &#Name{
                    margin-bottom:30px;
                }
            }
		
		button.btn-default{      
                font-size:14px;
                height:40px;
                width:120px;
                color:#fff;
                margin-bottom:30px;
                }
		}
		#Footer{
	       	p, a{
	            text-align:center;
	        }
	    }
	}
}

